* {
  border: none;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

a, a:link, a:visited {
  text-decoration: none;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: #0000;
}

input::-ms-clear {
  display: none;
}

button, input[type="submit"] {
  box-shadow: none;
  cursor: pointer;
  background: none;
  display: inline-block;
}

input:focus, input:active, button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

html, body {
  font-optical-sizing: auto;
  color: #fff;
  font-family: Inter, sans-serif;
  font-style: normal;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 110px;
}

.header {
  padding-block: 47px;
  position: fixed;
  left: 0;
  right: 0;
}

.header__content, .header__nav-list, .header__actions {
  display: flex;
}

.header__content {
  justify-content: space-between;
}

.header__actions, .header__nav-list {
  gap: 35px;
}

.header__nav-link {
  color: #fff;
}

.hero {
  background: linear-gradient(130deg, #191536 0%, #000 100%);
  height: 100vh;
}
